header {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 113px;
    /* background-color: beige; */
    color: #004863;
}

.headerWrapper {
    height: 100%;
    width: 69%;
    /* background-color: lightskyblue; */
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.bannerContainer {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    /* background-color: lightsalmon; */
    font-size: 35px;
}

.contactContainer {
    /* background-color: lightgreen; */
    display: flex;
    align-items: center;
}

.ui.button {
    padding: 15px 25px;
    color: #A1E5FF;
    background-color: #01627F;
}

.bannerContainer h1, .phoneContainer a {
    font-family: 'Epilogue', sans-serif;
    color: #004863;
    margin-bottom: 0;
    margin-top: 0;
}

.phoneContainer .ui.button {
    /* padding: 0; */
    color: white;
    font-size: 16px;
}

.phoneContainer .ui.button:hover {
    /* padding: 0; */
    color: #01627F;
}

.underline {
    display: inline;
    position: relative;
    overflow: hidden;
}

.underline:after {
    content: "";
    position: absolute;
    z-index: -1;
    right: 0;
    width: 0;
    bottom: -5px;
    background: #01627F;
    height: 4px;
    transition-property: width;
    transition-duration: 0.5s;
    transition-timing-function: ease-out;
}

.underline:hover:after, .underline:focus:after, .underline:active:after {
    left: 0;
    right: auto;
    width: 100%;
}

.bannerLinks {
    height: 30px;
    padding-left: 20px;
}

.bannerContainer img {
    height: 100%;
    margin-left: 5px;
    margin-right: 5px;
    /* width: 5px; */
}

.mobile-location {
    display: none;
}

@media only screen and (max-width: 1655px) and (min-width: 1201px) {
    .headerWrapper {
        width: 80%;
    }
}

@media only screen and (max-width: 1200px) and (min-width: 0px) {
